import React, { Component } from 'react';
import { connect } from 'react-redux'
import parse from 'html-react-parser'

class MyTable extends Component {

    constructor(props) {
        super(props);
    }
  
    render() {
        const { state } = this.props.state;

        return (
            <div>
              <table className='statetable'>
              <tbody>
              <tr>
                <td className='diag' ><div className='diagright'></div><div className='diagleft'></div></td>
                <td className={ state%2 == 0 ? 'blink' : ''}> ON</td>
                <td className={ state%2 == 1 ? 'blink' : ''}> OFF</td>
              </tr>

              { stateopts.tr.map( ( tr, i ) => (
                <tr key={i} >
                  <td className={state == i ? 'hl' : ''} >{ parse( tr.td1 ) }</td>
                  <td>{ parse( tr.td2 ) }</td>
                  <td>{ parse( tr.td3) }</td>
                </tr> 
              ) ) }

              </tbody>
              </table>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    state: state
});

const mapDispatchToProps = {
}

export default connect( mapStateToProps, mapDispatchToProps )(MyTable);
